/**
 * 
 * 
 */
export const splitText = (text, max = 10) => {
  if (typeof text !== 'string' || text.length <= max) {
    return text
  }

  var sub = text.substring(0, max)
  // 
  var chars = sub.match(/[\u0000-\u00FF]/g)
  if (!chars) {
    return sub
  }
  var len = 0;
  for (var i = sub.length; i < text.length; i++) {
    len += (/^[\u0000-\u00FF]$/.test(text[i]) ? 1 : 2)
    if (len + 1 >= chars.length) {
      break;
    }
  }

  return sub + text.substring(sub.length, sub.length + len)
}
/**
 *  00.00 
 * @param {} price
 */
export const transPrice = function (price) {
  return /^\d+$/.test(price) ? price + '.00' :  // 
  /^\d+\.\d$/.test(price) ? price + '0' : price  // 
}

/**
 *  url
 * @param {url } url
 * @param {* } reverse
 */
export const transURL = (url, reverse) => {
  if (typeof url !== 'string' || url === '')
    return ''
  if (reverse)
    return url.replace(/OxO/g, '/').replace(/OdO/g, '=').replace(/OwO/g, '?').replace(/OaO/g, '&')
  return url.replace(/\//g, 'OxO').replace(/\=/g, 'OdO').replace(/\?/g, 'OwO').replace(/&/g, 'OaO')
}

export const formatDate = function(date, year, time) {
  if (!date) { return '2019-1-1 0:0:0'}
  const d = new Date(date)
  if (Number.isNaN(d.getDay())) {
    return ''
  }
  var format = year ? d.getFullYear() + '-' : ''
  if (time) {
    return format + (d.getMonth() + 1) + '-' + d.getDate()
  }
  return (format + (d.getMonth() + 1) + '-' +
    d.getDate() + ' ' +
    d.getHours() + ':' +
    d.getMinutes() + ':' +
    d.getSeconds())
}
