const initState = {
  step: 1,
  account: '',
  storeName: '',
  idCard: '',
  reallyName: '',
  userId: -1
}

export default (state = initState, action) => {
  switch(action.type) {
    // 
    case 'STEP_CHANGE':
      return { ...state, step: action.payload }

    // 
    case 'STEP1_FINISH':
      return { ...state, step: 2, account: action.payload.account, userId: action.payload.userId }

    // 
    case 'STEP2_FINISH':
      return { ...state, step: 3, ...action.payload }

      default:
      return state
  }
}
