import React from 'react'
import { connect } from 'react-redux'
import { Route, Redirect } from 'react-router-dom'

import WaitVerify from './subpage/WaitVerify'
import { transURL } from '../../utils/tools'

// hydrate  bug
const waitComponent = typeof window === 'object' ? <WaitVerify message="...." /> : null

const PrivateRoute = ({component: Component, isAuthorized, isVerify, ...rest}) => (
  <Route {...rest} render={(props) => (
      isAuthorized ? <Component {...props} />
      : isVerify  //  token 
      ? waitComponent
      : <Redirect to={`/seller/login?prev=${transURL(props.location.pathname + props.location.search)}`} />
    )} />
)

function mapStateToProps(state) {
  return {
    isAuthorized: state.auth.isAuthorized,
    isVerify: state.auth.isVerify
  }
}
export default connect(mapStateToProps)(PrivateRoute)
