import React from 'react'
import { Link } from 'react-router-dom'

import './subpage/index.scss'
import { formatDate } from '../../../../utils/tools'

function splitGoodName(name) {
  return typeof(name) !== 'string' ? '' : name.length < 35 ? name : name.substring(0,34) + '...'
}

function judgeOrderState(route, order) {
  if (route !== '') {
    return route
  }
  if (order.sign === 0) {
    return ''
  }
  if (order.sign === 1 && order.comment === 0) {
    return ''
  }
  return ''
}

const OrderItem = (props) =>{
  const order = props.order
  /**--order
   * orderNo
   * number
   * monkey
   *
   * order.address
   * order,receiveName
   * goods
   *   id
   *   name (goodName)
   *   location (logo)
   *
   *     onClick={contactSeller(order.sellerId, order.sellerName, order.sellerAvatar)}
   */
  return <div className="order-item">
    <div className="o-i-head">
      <div className="oih-i oi-date">{formatDate(order.createTime, true, true)}</div>
      <div className="oih-i oi-no">{order.orderNo}</div>
      <div className="oih-i oi-sumprice">{order.money}</div>
    </div>
    <div className="oi-product">
      <div className="oip-i-wrap">
        <div className="oip-i oip-i-logo"><img width="80px" src={`${'http://118.126.108.36'}/image/goods/logo/${order.goods.location}_210x210q90.jpg`} alt={order.goods.name}/></div>
        <div className="oip-i oip-i-info">
          <div className="oip-i-goodname"><Link to={`/product_detail?goodId=${order.goods.id}`}>{splitGoodName(order.goods.name)}</Link></div>
        </div>
        <div className="oip-i oip-i-price">{order.money}</div>
        <div className="oip-i oip-i-number">{order.number}</div>
        <div className="oip-i oip-exp"></div>

        <div className="oip-i oip-i-operator">
         {
          props.route === '' || (order.pay === 1  && order.send === 0) ? <div className="opre-i-wrap">
            <button onClick={() => { props.sendPost() }} className="opre-i-sign"></button>
          </div> : <div className="opre-i-wrap">
            <span className="opre-i-waitsend">{judgeOrderState(props.route, order)}</span>
          </div>
         }
        </div>
      </div>
    </div>
  </div>
}

export default OrderItem
