import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

const Step3 = (props) => {
  return <div className="step3-wrap">
    <div className="s3-body">
      <div className="s3-info">
        <span></span>
        <span className="s3-storeName">{props.storeName}</span>
        <span></span>
      </div>
      <div><button onClick={() => {props.history.push('/seller/login')}} className="s3-ok"></button></div>
    </div>
  </div>
}

function mapStateToProps(state) {
  return {
    storeName: state.register.storeName
  }
}

export default withRouter(connect(mapStateToProps)(Step3))
