import React, { Component } from 'react'

class SendPost extends Component {
  constructor(props) {
    super(props)

    this.state = {
      postway: '',
      postno: ''
    }

    this.fieldChangeHandler = this.fieldChangeHandler.bind(this)
    this.postSubmit = this.postSubmit.bind(this)
  }

  fieldChangeHandler(e) {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  postSubmit(e) {
    e.preventDefault()
    if (this.state.postway === '' || this.state.postno === '') {
      return;
    }
    // 
    this.props.submit(this.state.postway, this.state.postno)
  }

  render() {
    return <div className="post-wrap">
      <form method="POST" onSubmit={this.postSubmit}>
        <div className="post-field f_wrap">
          <label className="f_L" htmlFor="postway"></label>
          <input className="f_r" onChange={this.fieldChangeHandler} autoComplete="off" type="text" placeholder="" value={this.state.postway} id="postway" name="postway"/>
        </div>
        <div className="post-field f_wrap">
          <label className="f_L" htmlFor="postno"></label>
          <input className="f_r" onChange={this.fieldChangeHandler} autoComplete="off" type="text" placeholder="" value={this.state.postno} id="postno" name="postno"/>
        </div>
        <div className="post-operator f_wrap">
          <button className="f_L" type="submit"></button>
          <button onClick={() => this.props.cancel()} className="post-cancel f_r"></button>
        </div>
      </form>
    </div>
  }
}

export default SendPost
