import React from 'react'
import classnames from 'classnames'

import { FILE_HOST } from '../../../config/config.js'

/**
 * abnormalOrder: 0
  looker: 0
  payMoney: 0
  payOrder: 0
  payUser: 0
 */
function GeneratorCurrentData(data) {
  return [
    { dataName: '', amount: data.payMoney || 0, logo: `${FILE_HOST}/image/seller/money.jpg` },
    { dataName: '', amount: data.abnormalOrder || 0, logo: `${FILE_HOST}/image/seller/foot.jpg` },
    { dataName: '', amount: data.payUser || 0, logo: `${FILE_HOST}/image/seller/person.jpg` },
    { dataName: '', amount: data.looker || 0, logo: `${FILE_HOST}/image/seller/look.jpg` },
    { dataName: '', amount: data.payOrder || 0, logo: `${FILE_HOST}/image/seller/pay.jpg` },
  ]
}

const CurrentData = (props) => {
  const currentData = new GeneratorCurrentData(props.data)

  return (
    <div className="data-wrap">
      <div className="da-head">
        <span></span>

      </div>
      <div className="data-box f_wrap">
      {
        currentData.map((item, index) => (
          <div className={classnames('data-item f_L', {
            'border-right': index % 2 === 0,
            'border-bottom': index < currentData.length - 1,
          })} key={index}>
            <div className="f_wrap">
              <div className="da-logo f_L"><img src={item.logo} alt={item.dataName}/></div>
              <div className="da-tit f_r">{item.dataName}</div>
            </div>
            <div className="da-amount">{item.amount}</div>
          </div>
        ))
      }
      </div>
    </div>
  )
}

export default CurrentData
