import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import './index.scss'
import { setAuthToken, setCurrentUser } from '../../utils/setAuth'
import { HOST } from '../../config/config'

class AppHeader extends Component {
	constructor() {
		super();
		this.state = {
      management: 'http://118.126.108.36/image/files/choice_perfect_manager.msi',
      chooseUrl: {
        management: 'http://118.126.108.36/image/files/choice_perfect_manager.msi', // 
        control: '/seller/panel',
        register: '/seller/register',
        login: '/seller/login',
        order: '/seller/login',
        waitSend: '/seller/wait_send'
      }
		}
	}
	// 
	loginOut() {
    var url = this.props.location.pathname
		setAuthToken(false); // axios token localStorage token
		setCurrentUser({}); // dispatch 
    this.props.history.push('/seller/login')  // 
  }

	render() {
    const auth = this.props.auth;
		const hello = (<ul className="ul-l">
						<li></li>
						<li>
							<Link to="/seller/login"></Link>
						</li>
						<li>
							<Link to="/seller/register"></Link>
						</li>
					</ul>)
		const userinfo = (<ul className="ul-l">
						<li>Hi<a href="/member">{auth.user.nickname}</a></li>
						<li>
							<a onClick={this.loginOut.bind(this)} href="javascript:void(0)"></a>
						</li>
					</ul>)
		return (
			<div className="app-header-wrap">
				<div className="app-header">
					{
						auth.isAuthorized ?
						userinfo :
						hello
					}
					<ul className="ul-r">
            <li><a href="/"></a></li>
						<li className="h-my">
              <a href="/member"></a>
              <div className="h-my-choi">
                <div><a href="/member/safety"></a></div>
                <div><a href="/member"></a></div>
                <div><a href="/member/myorder"></a></div>
                <div><a href="/member/address"></a></div>
              </div>
            </li>

            {
              !auth.isAuthorized || (auth.isAuthorized && auth.user.isSeller !== 1) ? (
                <li className="h-my">
                  <Link to="/seller/panel"></Link>
                    <div className="h-my-choi">
                      <div><Link to="/seller/register"></Link></div>
                      <div><a href={this.state.management}></a></div>
                  </div>
                </li>) : (
                  <li className="h-my">
                      <Link to="/seller/panel"></Link>
                      <div className="h-my-choi">
                        <div><Link to="/seller/panel/wait_send"></Link></div>
                        <div><Link to="/seller/panel/wait_send"></Link></div>
                        <div><a href={this.state.management}></a></div>
                    </div>
                  </li>
                )
            }
            <li className="h-my">
                <a href="javascript:void(0);"></a>
                <div className="h-my-choi">
                  <div><a href={this.state.chooseUrl.management}></a></div>
              </div>
            </li>
					</ul>
				</div>
			</div>
		)
	}
}

AppHeader.propTypes = {
	auth: PropTypes.object.isRequired
}

const mapStateToProps = state => {
	return {
		auth: state.auth
	}
}
export default withRouter(connect(mapStateToProps, null)(AppHeader))
