let initialState = {
  isAuthorized: false,
  isVerify: false, // token 
  user: {}
}

export default (state = initialState, action) => {
  switch(action.type) {
    /**
     * 
     */
    case 'SET_CURRENT_USER':
      return { ...state, user: action.payload, isAuthorized: true, isVerify: false }
    /**
     * 
     */
    case 'LOGIN_OUT':
      return { ...state, user: {}, isAuthorized: false, isVerify: false}

      /**
       * 
       */
    case 'VERIFYING':
      return { ...state, isVerify: true }

    default:
      return state
  }
}
