/**
 * get /api/seller/auth/good (checkState | goodState)
 */
import React, { Component } from 'react'
import Axios from 'axios';
import { connect } from 'react-redux'
import classnames from 'classnames'

import './index.scss'
import { SELLER_HOST } from '../../../../config/config'
import ProductItem from './ProductItem'
import { isEmpty } from '../../../../utils/validator'
import { alertMessageAction, confirmMessageAction } from '../../../../store//actions/messageActions'

class ProductManager extends Component {
  constructor(props) {
    super(props)
    this.state = {
      route: '',
      routes: [
        {rule: /\/in_seller$/, path: '/api/seller/auth/good?goodState=0', comment: ''},
        {rule: /\/un_seller$/, path: '/api/seller/auth/good?goodState=1', comment: ''},
        {rule: /\/chk_products$/, path: '/api/seller/auth/good?checkState=0', comment: ''},
      ],
      products: [
        {name: 'VR', logo: 'O8rORaHsYu_35422899.jpg', creaTime: '', price: 1000, isinseller: true},
        {name: '', logo: 'KL7reEqhUl_84629430.jpg', price: 1000, isinseller: true},
      ],
      page: 0
    }

    this.getProductData = this.getProductData.bind(this)
    this.hashNextPage = this.hashNextPage.bind(this)
    this.prevPage = this.prevPage.bind(this)
    this.nextPage = this.nextPage.bind(this)
  }

  componentWillMount() {
    this.getProductData(this.props.location.pathname)
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.location.pathname !== this.props.location.pathname) {
      this.getProductData(nextProps.location.pathname)
    }
  }

  /**
   * 
   * @param {*} id
   */
  downProduct(id, name) {
    this.props.confirmMessageAction(' ' + name, () => {
      Axios.post(`${SELLER_HOST}/api/seller/auth/good/down`, [id + ''])
        .then(({ data }) => {
          if (!data.success) {
            return this.props.alertMessageAction(data.message)
          }
          this.getProductData(this.props.location.pathname)
          this.props.alertMessageAction('')
        }).catch(err => {
          this.props.alertMessageAction('' + (err.message || ''))
        })
    }, ()=>{})

  }

  putProduct(id, name) {
    this.props.confirmMessageAction(' ' + name, () => {
      Axios.post(`${SELLER_HOST}/api/seller/auth/good/put`, [id + ''])
      .then(({ data }) => {
        if (!data.success) {
          return this.props.alertMessageAction(data.message)
        }
        this.getProductData(this.props.location.pathname)
        this.props.alertMessageAction('')
      }).catch(err => {
        this.props.alertMessageAction('' + (err.message || ''))
      })
    }, ()=>{})

  }

  /**
   * 
   * @param {} path
   */
  getProductData(path) {
    var routes = this.state.routes
    for (var i = 0, len = routes.length; i < len; i++) {
      if (routes[i].rule.test(path)) {
        this.setState({
          route: routes[i].comment,
          products: []
        })
        Axios.get(`${SELLER_HOST}${routes[i].path}`)
          .then(({ data }) => {
            if (!data.success) {
              return this.props.alertMessageAction(data.message)
            }
            this.setState({
              products: data.payload
            })
          })
          .catch(err => {
            this.props.alertMessageAction(err.message || '')
          })
        break;
      }
    }
  }

  hashNextPage() {
    var len = this.state.products.length
    var page = this.state.page
    return len % 5 === 0 ? len / 5 > page - 1 : Math.floor(len / 5) > page
  }

  /**
   * 
   */
  prevPage() {
    if (this.state.page > 0) {
      this.setState({
        page: this.state.page - 1
      })
    }
  }

  nextPage() {
    if (this.hashNextPage()) {
      this.setState({
        page: this.state.page + 1
      })
    }
  }

  render() {
    return <div className="om-wrap f_L">
      <div className="om-head">
        <div>{this.state.route}</div>
      </div>
      <div className="order-item-wrap" style={{width: '760px'}}>
        {
          !isEmpty(this.state.products) ? this.state.products.slice(this.state.page * 5, this.state.page * 5 + 5).map((product, index) => {
            return <ProductItem
              downProduct={this.downProduct.bind(this, product.id, product.name)}
              putProduct={this.putProduct.bind(this, product.id, product.name)}
              product={product} route={this.state.route}  key={index}/>
          }) : <div className="item-empty">
            {this.state.route}
          </div>
        }
        {
          !isEmpty(this.state.products) && this.state.products.length > 5 ? <div className="u-o-page">
            <div onClick={this.nextPage} className={classnames("u-o-next", {
              'u-o-next-void': !this.hashNextPage()
            })}></div>
            <div onClick={this.prevPage} className={classnames("u-o-prev", {
              'u-o-prev-void': this.state.page === 0
            })}></div>
          </div> : null
        }
      </div>
    </div>
  }
}

export default connect(null, { alertMessageAction, confirmMessageAction })(ProductManager)
