import React, { Component } from 'react'
import { Redirect, Link } from 'react-router-dom'
import { connect } from 'react-redux'

import './login.scss'
import { FILE_HOST } from '../../config/config'
import { transURL } from '../../utils/tools'
import AppHeader from '../../components/Header'
import Account from './subpage/Account'
import PhoneLogin from './subpage/PhoneLogin'
import Footer from '../../components/Footer/index.jsx'

class Login extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loginWay: 'phone',  // phone or account
      prev: '/seller/panel'
    }
  }
  componentWillMount() {
    // 
    var params = this.props.location.search
    var prev = this.state.prev

    if (typeof(params) === 'string' && /prev=.+/.test(params)) {
      let p = params.match(/prev=.+?(&|\b)/g)[0]
      if (p) {
        p = p.substring(5)
        prev = /&$/.test(p) ? p.substring(0, p.length - 1) : p
      }
    }
    // 
    if (this.props.isAuthorized) {
      return this.props.history.push(transURL(prev))
    }
    if (prev !== this.state.prev) {
      this.setState({
        prev: transURL(prev, true)
      })
    }

  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.isAuthorized) {
      return nextProps.history.push(this.state.prev)
    }
  }

  changeLoginWay(way) {
    if (way === this.state.loginWay) return;
    this.setState({
      loginWay: way
    })
  }

	render() {
		/**
		 * v4   
		 */
    if (this.props.isAuthorized === true) {  // 
      return <Redirect to={this.state.prev} />
    }
    // var path = this.props.location.pathname

    return (
			<div className="login-body">
        <AppHeader />
        <div className="login-head f_wrap">
          <div className="main-logo f_L"><img src={`${FILE_HOST}/image/seller/home_logo_blue.png`} alt=""/></div>
          <div className="login-msg f_r">61</div>
        </div>
				<div className="login-wrap">
					<div className="login-title"></div>
					<div className="login-form">
						<div className="form">
							<div className="form-t">
                <div onClick={this.changeLoginWay.bind(this, 'phone')} className={this.state.loginWay === 'phone' ? 't-l f1' : 't-l f2'}></div>
                <div onClick={this.changeLoginWay.bind(this, 'account')} className={this.state.loginWay === 'account' ? 't-l f1' : 't-l f2'}></div>
							</div>
							<main>
                {
                  this.state.loginWay === 'phone'
                  ? <PhoneLogin prev={this.state.prev} />
                  : <Account prev={this.state.prev} />
                }
							</main>
              <div className="to-registory">
                <Link to="/seller/register"></Link>
              </div>
						</div>
					</div>
				</div>
        <Footer />
			</div>
		)
	}
}

function mapStateToProps(state) {
	return {
    isAuthorized: state.auth.isAuthorized,
    isVerify: state.auth.isVerify,
	}
}

export default connect(mapStateToProps, null)(Login)
