import React, { Component } from 'react'
import classnames from 'classnames'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import Axios from 'axios'

import { step2FinishAction } from '../../../store/actions/registerActions'
import { SELLER_HOST } from '../../../config/config';

class Step2 extends Component {
  constructor(props) {
    super(props)
    this.state = {
      storeName: '',
      idCard: '',
      reallyName: '',
      errors: {storeName: ''}
    }

    this.validator = this.validator.bind(this)
    this.fieldChangeHandler = this.fieldChangeHandler.bind(this)
    this.checkStoreNameRepeat = this.checkStoreNameRepeat.bind(this)
    this.step2SubmitHandler = this.step2SubmitHandler.bind(this)
  }

  validator() {
    var ans = {}
    if (!this.state.storeName) {
      ans.storeName = ''
    }
    if (!/^([1-9]\d{14}|[1-9]\d{17})$/.test(this.state.idCard)) {
      ans.idCard = ''
    }
    if (!/^.{1,5}$/.test(this.state.reallyName)) {
      ans.reallyName = '1-5'
    }
    return ans
  }

  fieldChangeHandler(e) {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  /**
   * ajax 
   */
  checkStoreNameRepeat() {
    // console.log(this.state.storeName)
    // if (!this.state.storeName) return;
    // Axios.post('', {storeName: this.state.storeName})
    //   .then(({ data }) => {
    //     console.log(data)
    //   }).catch(err => {
    //     console.error(err)
    //   })

    // this.props.step2FinishAction({
    //   storeName: this.state.storeName,
    //   reallyName: this.state.reallyName
    // })
  }

  /**
   * 
   */
  step2SubmitHandler() {
    var ans = this.validator()
    if (Object.keys(ans).length !== 0) {
      return this.setState({ errors: ans })
    }
    console.log('')
    var req = {
      userId: this.props.register.userId + '',
      merchantName: this.state.storeName,
      relName: this.state.relName,
      idCard: this.state.idCard
    }
    Axios.post(`${SELLER_HOST}/api/seller/store`, req)
      .then(({ data }) => {
        if (!data.success) {
          return alert(data.message)
        }
        this.props.step2FinishAction()
        // console.log(data)
      }).catch(err => {
        alert(err.message || 'server busy')
      })
  }

  render() {
    return <div className="step2-wrap">
      <div className="step2-item f_wrap">
        <label className="f_L" htmlFor="storeName"></label>
        <input onBlur={this.checkStoreNameRepeat} onChange={this.fieldChangeHandler} className="f_r" value={this.state.storeName} placeholder="" autoComplete="off" name="storeName" type="text" id="storeName"/>
      </div>
      {
        this.state.errors.storeName && <div className="form-err">{this.state.errors.storeName}</div>
      }
      <div className="step2-item f_wrap">
        <label className="f_L" htmlFor="idCard"></label>
        <input onChange={this.fieldChangeHandler} className="f_r" value={this.state.idCard} placeholder="" autoComplete="off" name="idCard" type="text" id="idCard"/>
      </div>
      {
        this.state.errors.idCard && <div className="form-err">{this.state.errors.idCard}</div>
      }
      <div className="step2-item f_wrap">
        <label className="f_L" htmlFor="reallyName"></label>
        <input onChange={this.fieldChangeHandler} className="f_r" value={this.state.reallyName} placeholder="" autoComplete="off" name="reallyName" type="text" id="reallyName"/>
      </div>
      {
        this.state.errors.reallyName && <div className="form-err">{this.state.errors.reallyName}</div>
      }
      <div>
        <button onClick={this.step2SubmitHandler} className={classnames('next-btn', {
          'next-wait': Object.keys(this.validator()).length !== 0
        })}></button>
      </div>
    </div>
  }
}

Step2.propTypes = {
  register: PropTypes.object.isRequired
}

function mapStateToProps(state) {
  return {
    register: state.register
  }
}

export default connect(mapStateToProps, { step2FinishAction })(Step2)
