/**
 * goodsReducers
 * Simple React Snippet
 */
import { isEmpty } from '../../utils/validator.js'

let initialState = {
  type: [],
  categoryChoose: []
}

export default (state = initialState, action) => {
  switch(action.type) {
    /**
     * 
     */
    case 'SET_BIG_TYPE':
      return { ...state, type: action.payload }

    /**
     * 
     */
    case 'SET_SMAILL_TYPE':
      let bigType = state.type.find(i => i._id === action.payload.bigid)
      if (isEmpty(state.type) || isEmpty(bigType)) {
        return state
      }
      bigType.smaillType = action.payload.smaillType
      return state

    /**
     * 
     */
    case 'SET_CATEGORY_CHOOSE':
      return { ...state, categoryChoose: action.payload }

    default:
      return state
  }
}
