import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Switch, Route, Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import classnames from 'classnames'

import { FILE_HOST, SELLER_HOST } from '../../config/config'
import { splitText } from '../../utils/tools.js'
import { toggleChatAction } from '../../store/actions/chatActions'
import Axios from 'axios'

import './index.scss'
import CurrentData from './subpage/CurrentData'
import Backlog from './subpage/Backlog'
import Footer from '../../components/Footer/index.jsx'
import AppHeader from '../../components/Header'
import OrderManager from './subpage/OrderManager/index.jsx'
import ProductManager from './subpage/ProductManager/index.jsx'

class SellerHome extends Component {
	constructor(props) {
    super(props)

    this.state = {
      route: '',
      currentData: {}, // 
      sellerData: {}, // 
      // 
      operators: [
        { routeName: '', routePath: 'transaction', logo: `${FILE_HOST}/image/seller/jiaoyi.jpg`, navs: [
          { navName: '', path: '/seller/panel/all_order'},
          { navName: '', path: '/seller/panel/wait_comment'},
        ] },
        { routeName: '', routePath: 'order', logo: `${FILE_HOST}/image/seller/yunying.jpg`, navs: [
          { navName: '', path: '/seller/panel/wait_pay'},
          { navName: '', path: '/seller/panel/wait_send'},
          { navName: '', path: '/seller/panel/wait_sign'},
          { navName: '', path: '/seller/panel/wait_comment'},
        ] },
        { routeName: '', routePath: 'express', logo: `${FILE_HOST}/image/seller/wuliu.jpg`, navs: [
          { navName: '', path: '/seller/panel/wait_send'},
          { navName: '', path: '/seller'},
          { navName: '', path: '/seller'},
        ] },
        { routeName: '', routePath: 'product', logo: `${FILE_HOST}/image/seller/baobei.jpg`, navs: [
          { navName: '', path: '/seller/category'},
          { navName: '', path: '/seller/panel/in_seller'},
          { navName: '', path: '/seller/panel/un_seller'},
          { navName: '', path: '/seller/panel/chk_products'},
        ] },
        { routeName: '', routePath: 'store', logo: `${FILE_HOST}/image/seller/dianpu.jpg`, navs: [
          { navName: '', path: '/seller'},
          { navName: '', path: '/seller'},
          { navName: '', path: '/seller'},
          { navName: '', path: '/seller'},
        ] },
      ]
    }
	}

  componentWillMount() {

    var currentRequest = () => Axios.get(`${SELLER_HOST}/api/seller/auth/ydata`)
    var sellerRequest = () => Axios.get(`${SELLER_HOST}/api/seller/auth/good/data`)
    Axios.all([currentRequest(), sellerRequest()])
      .then(Axios.spread((currentRes, sellerRes) => {
        if (currentRes.data.success && sellerRes.data.success) {
          this.setState({
            currentData: { ...currentRes.data.payload },
            sellerData: { ...sellerRes.data.payload }
          })
        }
      })).catch(err => {
        console.error(err)
      })
  }

  handlerRouteChange(route) {
    this.setState({
      route: this.state.route === route ? '' : route
    })
  }

  /**
   * 
   */
  toggleChatWindowHandler() {
    console.log(this.props.hideChat)
    this.props.toggleChatAction(!this.props.hideChat)
  }

	render() {
    const user = this.props.user
    // console.log('', this.props.notReadChatLength)

		return (
			<div className="home">
      <AppHeader />
        <div className="head-wrap" style={{border: '1px solid blue'}}>
          <div className="head-content f_wrap">
            <div className="head-l f_L">
              <span className="h-f1"></span>
              <span className="h-f2"></span>
            </div>

            <div className="head-r f_r f_wrap">
              <div className="h-r-item f_L"><div></div></div>
              <div className="h-r-item f_L"><div></div></div>
              <div className="h-r-item f_L"><div></div></div>
            </div>
          </div>
        </div>

        <div className="h-body f_wrap">
          <div className="list1-wrap f_L">
            {/*  */}
            <div className="user-info-wrap f_wrap">
              <div className="u-avatar f_L"><img src={`${FILE_HOST}/image/store/logo/${user.storeLogo}`} alt={user.storeName}/></div>
              <div className="u-storename-wrap f_L">
                <div className="u-storename"><span className="f2">{splitText(user.storeName, 7)}</span></div>
              </div>
              <div onClick={this.toggleChatWindowHandler.bind(this)} className="chat-bar">
                {
                  //  Logo
                  this.props.notReadChatLength > 0 ? (
                    <div className="notread-msg">{this.props.notReadChatLength}</div>
                  ) : (
                    <img src={`${FILE_HOST}/image/ui/frog.png`} alt="chat"/>
                  )
                }
              </div>
            </div>
            <div className="oper-wrap">
            {
              this.state.operators.map((operator, index) => (
                <div className="nav-item-wrap" key={index}>
                  <div className="nav-item f_wrap" onClick={this.handlerRouteChange.bind(this, operator.routePath)}>
                    <div className="nav-logo f_L"><img src={operator.logo} alt={operator.routeName}/></div>
                    <div className={classnames("oper-bar f_L", {
                      'bar-seleced': this.state.route === operator.routePath
                    })}>{operator.routeName}</div>
                    <div className="nav-icon f_r"><img src={`${FILE_HOST}/image/ui/${this.state.route === operator.routePath ? 'down' : 'right'}.png`}/></div>
                  </div>
                  <div className="nav-box">
                    {/* { React.createElement(operator.component, {route: this.state.route}) } */}
                    {
                      this.state.route === operator.routePath && operator.navs.map((nav, index) => (
                        <div className="nav-cho-i" key={index}><Link to={nav.path}>{nav.navName}</Link></div>
                      ))
                    }
                  </div>
                </div>
              ))
            }
            </div>
          </div>
          <div>
            <Switch>
              <Route path="/seller/panel/all_order" component={OrderManager}/>
              <Route path="/seller/panel/wait_pay" component={OrderManager}/>
              <Route path="/seller/panel/wait_send" component={OrderManager}/>
              <Route path="/seller/panel/wait_sign" component={OrderManager}/>
              <Route path="/seller/panel/wait_comment" component={OrderManager}/>
              <Route path="/seller/panel/in_seller" component={ProductManager}/>
              <Route path="/seller/panel/un_seller" component={ProductManager}/>
              <Route path="/seller/panel/chk_products" component={ProductManager}/>
              <Route path="/seller/panel" render={() => {
                return (<div>
                  <div className="current-data f_L">
                    <CurrentData data={this.state.currentData} />
                  </div>

                  <div className="backlog f_L">
                    <Backlog data={this.state.sellerData} />
                  </div>
                </div>)
              }} />
            </Switch>
          </div>

        </div>
        <Footer />
			</div>
		)
	}
}

SellerHome.propTypes = {
  user: PropTypes.object.isRequired,
  hideChat: PropTypes.bool.isRequired,
  notReadChatLength: PropTypes.number.isRequired
}

const setStateToProps = (state) => {
	return {
    user: state.auth.user,
    hideChat: state.chat.hideChat,
    // 
    notReadChatLength: state.chat.messagesNotRead.reduce((prev, now) => prev + now.notRead, 0)
	}
}

export default connect(setStateToProps, { toggleChatAction })(SellerHome)
