import React from 'react'
import { Link } from 'react-router-dom'
import { FILE_HOST } from '../../../../config/config'

import { formatDate, transPrice } from '../../../../utils/tools'

function splitGoodName(name) {
  return typeof(name) !== 'string' ? '' : name.length < 35 ? name : name.substring(0,34) + '...'
}

const ProductItem = props => {
  const product = props.product
  return <div className="order-item">
  <div className="o-i-head">
    <div className="oih-i oi-date">{formatDate(product.creatTime, true, true)}</div>
    <div className="oih-i oi-no" style={{textAlign: 'left'}}>ID{product.id}</div>
    <div className="oih-i oi-sumprice">{transPrice(product.price)}</div>
  </div>
  <div className="oi-product">
    <div className="oip-i-wrap">
      <div className="oip-i oip-i-logo"><img width="80px" src={`${FILE_HOST}/image/goods/logo/${product.location}_210x210q90.jpg`} alt={product.name}/></div>
      <div className="oip-i oip-i-info">
        <div className="oip-i-goodname"><Link to={`/product_detail?goodId=${product.id}`}>{splitGoodName(product.name)}</Link></div>
      </div>
      <div className="oip-i oip-i-price"></div>
      <div className="oip-i oip-i-number">{product.buyNo}</div>
      <div className="oip-i oip-exp"></div>

      <div className="oip-i oip-i-operator">
       {
        props.route === '' ? <div className="opre-i-wrap">
          <button onClick={props.downProduct} className="opre-i-sign"></button>
        </div> :
        props.route === '' ? <div className="opre-i-wrap">
          <button onClick={props.putProduct} className="opre-i-sign"></button>
        </div> :
        <div className="opre-i-wrap">
          <span className="opre-i-waitsend"></span>
        </div>
       }
      </div>
    </div>
  </div>
</div>
}

export default ProductItem
