import store from '../store.js'

export const setBigType = (bigType) => dispatch => {
  dispatch({
    type: 'SET_BIG_TYPE',
    payload: bigType
  })
}

export const setProductType = (bigType) => {
  store.dispatch({
    type: 'SET_BIG_TYPE',
    payload: bigType
  })
}


export const setSmaillType = (bigid, smaillType) => dispatch => {
  dispatch({
    type: 'SET_SMAILL_TYPE',
    payload: {
      bigid,
      smaillType
    }
  })
}

export const setCategoryChoose = (choose) => dispatch => {
  dispatch({
    type: 'SET_CATEGORY_CHOOSE',
    payload: choose
  })
}
