import React, { Component } from 'react'
import PropTypes from 'prop-types'
import axios from 'axios'
import { connect } from 'react-redux'
import classnames from 'classnames'
import FreeScrollBar from 'react-free-scrollbar'

import './index.scss'
import Footer from '../../components/Footer/index'
import AppHeader from '../../components/Header/index'

import { HOST } from '../../config/config.js'
import { setBigType, setSmaillType } from '../../store/actions/goodsAction'
import { alertMessageAction } from '../../store/actions/messageActions'
import { setCategoryChoose } from '../../store/actions/goodsAction'

class Category extends Component {
  constructor(props) {
    super(props)

    this.state = {
      choose: [],
      showSmaill: false,
      showDetail: false,
      errors: {
        category: '', // ''
      }
    }

    this.handlerCategorySubmit = this.handlerCategorySubmit.bind(this)
    this.isFinishChoose = this.isFinishChoose.bind(this)
  }

  componentWillMount() {
    if (typeof(window) === 'object') {
      // 
      var productType = window.localStorage.getItem('productType')
      try {
        if (productType) {
          productType = JSON.parse(productType)
          return this.props.setBigType(productType)
        }
      } catch(e) {
        console.error('localStorage category error', e)
      }
    }
    // 
    axios.get(`${HOST}/api/goods/type?type=big`)
      .then(({ data }) => {
        if (!data.success) {
          // 
          return;
        }
        this.props.setBigType(data.payload)
        // 
        if (typeof(window) === 'object') {
          window.localStorage.setItem('productType', JSON.stringify(data.payload))
        }
      })
      .catch(err => {
        this.setState({
          errors: {...this.state.errors, category: ''}
        })
      })
  }

  /**
   * 
   * @param  {[type]} itemId [id]
   * @param  {[type]} type   ['big','smaill','detail']
   * @return {[type]}        [description]
   */
  handlerTypeClick(itemId, type) {
    let choose = this.state.choose

    // 
    if (type === 'big' && this.state.choose[0] !== itemId) {
      if (!this.props.type.find(i => i._id === itemId).smaillType) {
        axios.get(`${HOST}/api/goods/type?type=smaill&bigid=${itemId}`)
        .then(({ data }) => {
          if (!data.success) {
            // 
            return;
          }
          // 
          this.props.setSmaillType(itemId, data.payload)

          this.setState({
            choose: [itemId],
            showSmaill: true,
            showDetail: false
          }, () => {
            if (typeof(window) === 'object') {
              console.log('setSmailType')
              window.localStorage.setItem('productType', JSON.stringify(this.props.type))
            }
          })
        })
        .catch(err => {

        })
        return;
      }else {
        return this.setState({
          choose: [itemId],
          showSmaill: true,
          showDetail: false
        })
      }
    }

    // 
    if (type === 'smaill' && this.state.choose[1] !== itemId) {
      this.setState({
        choose: [choose[0], itemId],
        showDetail: true,
      })
      return;
    }

    // 
    if (type === 'detail' && this.state.choose[2] !== itemId) {
      choose[2] = itemId
      this.setState({
        choose,
      })
    }

  }

  /**
   * 
   */
  isFinishChoose() {
    return [1,1].reduce((one, _, index) => one && !!this.state.choose[index + 1], !!this.state.choose[0])
  }

  /**
   * 
   */
  handlerCategorySubmit() {
    //  redux 
    if (!this.isFinishChoose()) {
      return this.props.alertMessageAction('')
    }
    this.props.setCategoryChoose(this.state.choose)
    this.props.history.push('/seller/product_issue')
  }

  render() {
    const category = this.props.type

    // 
    const bigType = this.state.choose[0] ?
                      category.find(i => i._id === this.state.choose[0]) : null
    // 
    const smaillType = bigType && this.state.showSmaill ?
                        bigType.smaillType : null

    // 
    const detailType = smaillType && this.state.showDetail && this.state.choose[1] ?
                        smaillType.find(i => i._id === this.state.choose[1]).detail : null

    return (<div className="category">
        <AppHeader />
        <div className="category-wrap">
          <h2 className="category-head"></h2>
          <div>
            <div>
              {
                this.state.errors.category && <div>{this.state.errors.category}</div>
              }
            </div>
            <div className="cho-wrap">
              <div className="cho-onece">
                {
                  category && category.map((bigItem, index) => (
                    <div className={classnames('cho-item', {
                      'cho-chosen': this.state.choose[0] === bigItem._id
                    })} onClick={this.handlerTypeClick.bind(this, bigItem._id, 'big')} key={index}>{bigItem.bigName}</div>
                  ))
                }
              </div>
              <div className="cho-onece" style={{borderColor: smaillType ? null : '#ccc'}}>
                <FreeScrollBar>
                {
                  smaillType && smaillType.map((smaillItem, index) => (
                    <div className={classnames('cho-item', {
                      'cho-chosen': this.state.choose[1] === smaillItem._id
                    })} onClick={this.handlerTypeClick.bind(this, smaillItem._id, 'smaill')} key={index}>{smaillItem.smaillName}</div>
                  ))
                }
                </FreeScrollBar>
              </div>
              <div className="cho-onece" style={{borderColor: detailType ? null : '#ccc'}}>
                <FreeScrollBar>
                {
                  detailType && detailType.map((detailItem, index) => (
                    <div className={classnames('cho-item', {
                      'cho-chosen': this.state.choose[2] === detailItem._id
                    })} onClick={this.handlerTypeClick.bind(this, detailItem._id, 'detail')} key={index}>{detailItem.detailName}</div>
                  ))
                }
                </FreeScrollBar>
              </div>
            </div>

            {
              bigType && <div className="current-cate-wrap">
                <div className="curr-cate-box f_wrap">
                  <div className="f_L"></div>
                  <div className="f_L f_wrap">
                    {
                      this.state.choose.map((typeId, index) => {
                        return index === 0 ?
                                <div className="curr-item f_L" key={index}>{bigType.bigName}</div> :
                                index === 1 ?
                                <div className="curr-item f_L" key={index}><span className="curr-gt">&gt;</span><span>{smaillType.find(i => i._id === typeId).smaillName}</span></div> :
                                <div className="curr-item f_L" key={index}><span className="curr-gt">&gt;</span><span>{detailType.find(i => i._id === typeId).detailName}</span></div>
                      })
                    }
                  </div>
                </div>
              </div>
            }


            <div>
              <div onClick={this.handlerCategorySubmit} className={classnames('c-submit', {
                'c-submit-disable': !this.isFinishChoose(),
              })}></div>
            </div>
          </div>

          <div className="readme">
            <FreeScrollBar>
              <p></p>
              <p> </p>
              <span>1.</span><br />
              <span>2.choice.com()</span><br />
              <span>3. </span><br />
              <span>4.</span><br />
              <p> </p>
              <span>1. </span><br />
              <span>2. </span><br />
            </FreeScrollBar>
          </div>

        </div>
        <Footer />

    </div>)
  }
}

Category.propTypes = {
  type: PropTypes.array.isRequired,
  setBigType: PropTypes.func.isRequired,
  setSmaillType: PropTypes.func.isRequired,
}

function setStateToProps(state) {
  return {
    type: state.goods.type
  }
}

export default connect(setStateToProps, { setBigType, setSmaillType, alertMessageAction, setCategoryChoose })(Category)
