import React from 'react';
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { Route, Switch } from 'react-router-dom'
import axios from 'axios'

import store from './store/store.js'
import SellerHome from './containers/SellerHome/index.jsx'
import Login from './containers/Login/index.jsx'
import Register from './containers/Register/index.jsx'
import Category from './containers/Category/index.jsx'
import ProductIssue from './containers/ProductIssue/index'
import PrivateRoute from './components/PrivateRoute/index'

import Chat from './components/Chat'
import MessageBox from './components/MessageBox/index'
import Confirm from './components/MessageBox/Confirm'

import { setProductType } from './store/actions/goodsAction'
import { setAuthToken, setCurrentUser } from './utils/setAuth'
import { HOST } from './config/config'

if (typeof(window) === 'object') {
  // 
  console.log('%c','color:#3680f1;font-size:30px;font-weight:bold;')
  console.log('%c ','font-size:20px;color:#777777;font-weight:400;')

  //  token
  const jwtToken = window.localStorage.jwtToken;
  if (!jwtToken) {
    setAuthToken(false);
    setCurrentUser({});
  } else {
    axios.defaults.headers.common["Authorization"] = jwtToken //  token
    store.dispatch({ type: 'VERIFYING' })

    // token
    axios.get(`${HOST}/api/users/current`)
      .then(res => {
        if (!res.data.success) {
          setAuthToken(false)
          setCurrentUser({})
        } else {
          // token 
          setAuthToken(res.data.payload.token)
          setCurrentUser(res.data.payload.user)
        }
      })
      .catch(err => {
        console.warn('at verify local token:', err)
        setAuthToken(false);
        setCurrentUser({});
      })
  }

  // 
  var productType = window.localStorage.getItem('productType')
  try {
    setProductType(JSON.parse(productType))
  } catch(e) {

  }
}

// <Provider store={ store }>
// </Provider>
const App = (props) => {
  return (
  		<div>
          <Switch>
            <PrivateRoute path="/seller/panel" component={SellerHome} />
            <Route exact path="/seller/login" component={Login} />
            <Route exact path="/seller/register" component={Register} />
            <PrivateRoute exact path="/seller/category" component={Category} />
            <PrivateRoute exact path="/seller/product_issue" component={ProductIssue} />
            {/*  */}
            <Redirect exact from="/seller" to="/seller/panel" />
            <Redirect exact from="/*" to="/seller/panel" />
          </Switch>
          {
            // 
            props.hideChat ? null : <Chat />
          }
          {
            !props.showAlert ? null : <MessageBox />
          }
          {
            !props.showConfirm ? null : <Confirm />
          }
  		</div>

  )
}

function mapStateToProps(state) {
  return {
    hideChat: state.chat.hideChat,
    showAlert: state.messageBox.showAlert,
    showConfirm: state.messageBox.showConfirm,
  }
}

export default connect(mapStateToProps)(App)
