import React, { Component } from 'react'
import Axios from 'axios'
import { connect } from 'react-redux'
import classnames from 'classnames'

import { SELLER_HOST } from '../../../../config/config'
import { isEmpty } from '../../../../utils/validator'
import { alertMessageAction } from '../../../../store/actions/messageActions'

import OrderItem from './OrderItem.jsx'
import SendPost from './SendPost.jsx'

class OrderManager extends Component {
  constructor(props) {
    super(props)

    this.state = {
      routes: [
        {rule: /all_order$/, path: '/api/seller/auth/order', comment: ''},
        {rule: /wait_pay$/, path: '/api/seller/auth/order?payState=0', comment: ''},
        // {rule: /quit_pay$/, path: '/api/seller/auth/order?quitState=0'},
        {rule: /wait_send$/, operator: 'send', path: '/api/seller/auth/order?sendState=0',comment: ''},
        {rule: /wait_sign$/, path: '/api/seller/auth/order?signState=0', comment: ''},
        {rule: /wait_comment$/, path: '/api/seller/auth/order?commentState=0', comment: ''},
      ],
      orders: [],
      route: '',
      sendPost: false,
      orderId: '',
      page: 0
    }

    this.getOrders = this.getOrders.bind(this)
    this.hashNextPage = this.hashNextPage.bind(this)
    this.prevPage = this.prevPage.bind(this)
    this.nextPage = this.nextPage.bind(this)
  }

  componentWillMount() {
    this.getOrders(this.props.location.pathname)
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.location.pathname === this.props.location.pathname) {
      return;
    }
    this.getOrders(nextProps.location.pathname)
  }

  /**
   *  url 
   * @param {  } path
   */
  getOrders(path) {
    var routes = this.state.routes
    for (var i = 0, len = routes.length; i < len; i++) {
      if (routes[i].rule.test(path)) {
        this.setState({
          route: routes[i].comment,
          orders: []
        }, () => {
          Axios.get(`${SELLER_HOST}${routes[i].path}`)
            .then(({ data }) => {
              console.log(data)
              if (!data.success) {
                return this.setState({
                  errors: { data: data.message }
                })
              }
              this.setState({
                orders: data.payload,
                route: routes[i].comment
              })
            })
            .catch(err => {
              this.props.alertMessageAction(' ' + (err.message ||  ''))
            })
        })
        break;
      }
    }
  }

  sendPostChangeHandler(orderId) {
    this.setState({ sendPost: true, orderId: orderId })
  }

  /**
   * 
   */
  postSubmitHandler(postway, postno) {
    var req = {
      orderId: this.state.orderId,
      postWay: postway,
      expNumber: postno
    }
    Axios.post(`${SELLER_HOST}/api/seller/auth/order/send`, req)
      .then(({ data }) => {
        if (!data.success) {
          return this.props.alertMessageAction(data.message)
        }
        this.setState({
          sendPost: false
        }, () => {
          this.getOrders(this.props.history.location.pathname)
          this.props.alertMessageAction('')
        })
      }).catch(err => {
        this.props.alertMessageAction(err.message || '');
      })
  }

  hashNextPage() {
    var len = this.state.orders.length
    var page = this.state.page
    return len % 5 === 0 ? len / 5 > page - 1 : Math.floor(len / 5) > page
  }

  /**
   * 
   */
  prevPage() {
    if (this.state.page > 0) {
      this.setState({
        page: this.state.page - 1
      })
    }
  }

  nextPage() {
    if (this.hashNextPage()) {
      this.setState({
        page: this.state.page + 1
      })
    }
  }

  render() {
    return <div className="om-wrap f_L">
      <div className="om-head">
        <div>{this.state.route}</div>
      </div>
      <div className="order-item-wrap" style={{width: '760px'}}>
        {
          !isEmpty(this.state.orders) ? this.state.orders.slice(this.state.page * 5, this.state.page * 5 + 5).map((order, index) => {
            return <OrderItem sendPost={this.sendPostChangeHandler.bind(this, order.id)} order={order} route={this.state.route}  key={index}/>
          }) : <div className="item-empty">
            {this.state.route}
          </div>
        }
        {
          // 
          !isEmpty(this.state.products) && this.state.products.length > 5 ? <div className="u-o-page">
            <div onClick={this.nextPage} className={classnames("u-o-next", {
              'u-o-next-void': !this.hashNextPage()
            })}></div>
            <div onClick={this.prevPage} className={classnames("u-o-prev", {
              'u-o-prev-void': this.state.page === 0
            })}></div>
          </div> : null
        }
        {
          this.state.sendPost ?
          <SendPost
            cancel={() => this.setState({sendPost: false})}
            submit={this.postSubmitHandler.bind(this)}
           /> : null
        }
      </div>
    </div>
  }
}

export default connect(null, { alertMessageAction })(OrderManager)
