
export const step1FinishAction = (phone, userId) => dispatch => {
  dispatch({
    type: 'STEP1_FINISH',
    payload: {
      account: phone,
      userId
    }
  })
}

export const step2FinishAction = data => dispatch => {
  dispatch({
    type: 'STEP2_FINISH',
    payload: data
  })
}
