import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import './index.scss'
import AppHeader from '../../components/Header/index'
import Footer from '../../components/Footer/index'
import { FILE_HOST, HOST } from '../../config/config';
import axios from 'axios';
import { alertMessageAction, confirmMessageAction } from '../../store/actions/messageActions'

/**
 *   table 
 * @param {} arr [[a,b],[c,d]]
 */
function specValueArrToTableArr(arr) {
	if (arr.length === 0) {
		// 
		return [[]]
	}
	var table = [] // n 
	var indexArr = []
	var indexAdd = [];
	for (var i = 0, len1 = arr.length; i < len1; i++) {
		indexArr[i] = 0;
		indexAdd[i] = (function (){
			var max = arr[i].length
			var index = i;
			return function (){
				if (indexArr[index] + 1 >= max) {
					indexArr[index] = 0
					if (index > 0) {
						indexAdd[index -1]()
					}
				}else {
					indexArr[index]++
				}
			}
		}())
	}
	var afterRow = []
	var rows = 1
	for (var i = 0, len1 = arr.length; i < len1; i++) {
		afterRow[i] = 1;
		rows *= arr[i].length
		for (var j = i + 1; j < arr.length; j++) {
			afterRow[i] *= arr[j].length
		}
	}
	for (var x = 0; x < rows; x++) {
		table[x] = [];
		for (var i = 0, len1 = arr.length; i < len1; i++) {
			if (x % afterRow[i] === 0) {
				table[x].push({
					value: arr[i][indexArr[i]],
					rowspan: afterRow[i]
				})
			}
		}
		indexAdd[indexAdd.length -1]()
	}
	return table
}

class ProductIssue extends Component {
  constructor(props) {
    super(props)

    this.state = {
      choose: [],
      goodName: '',
      goodFrom: '',
      // 
      properties: [
        {name: '', value: ['','']},
        {name: '', value: ['','']}
      ],
      // 
      specInfo: [
        {amount: 0, price: 0}
      ],
      // 
      smaillPicture: [''],
      infoPicture: [''],
      logo: '',
      errors: {}
    }

    this.filterProperties = this.filterProperties.bind(this)
    this.submitProductIssue = this.submitProductIssue.bind(this)
    this.fieldChangeHandler = this.fieldChangeHandler.bind(this)
  }

  componentWillMount() {
    var choose = this.props.categoryChoose
    // 
    var flag = [1,1,1].reduce((prev, _, index) => prev && !!choose[index], true)
    if (!flag) {
      return this.props.history.push('/seller/category')
    }
    var bigType = this.props.type.find((i) => i._id === choose[0])
    var smaillType = bigType.smaillType.find((i) => i._id === choose[1])
    var detailType = smaillType.detail.find((i) => i._id === choose[2])

    this.setState({
      choose: [bigType, smaillType, detailType]
    })

  }

  fieldChangeHandler(e) {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  /**
   * 
   * @param {} index
   * @param {} e
   */
  propNameChangeHandler(index, e) {
    var properties = this.state.properties
    properties[index].name = e.target.value
    this.setState({
      properties
    })
  }

  propValueChangeHandler(i, j, e) {
    var properties = this.state.properties
    properties[i].value[j] = e.target.value
    this.setState({
      properties
    })
  }

  /**
   * 
   */
  propNameBlurHandler(i) {
    var props = this.state.properties
    if (props[props.length-1].name && props[i].name) {
      props.push({name: '', value: ['']})
      this.setState({
        properties: props
      })
    }
  }
  /**
   * 
   */
  propValueBlurHandler(i, j) {
    var props = this.state.properties
    if (props[i].name && props[i].value[props[i].value.length -1]) {
      props[i].value.push('')
      this.setState({
        properties: props
      })
    }
  }

  /**
   * 
   */
  propNameCancelHandler(i) {
    if (i === 0) return;
    var props = this.state.properties
    props.splice(i, 1)
    this.setState({
      properties: props
    })
  }

  /**
   * 
   */
  propValueCancelHandler(i, j) {
    if (j === 0) return;
    var props = this.state.properties
    props[i].value.splice(j, 1)
    this.setState({
      properties: props
    })
  }

  /**
   * 
   */
  specInfoChangeHandler(index, e) {
    var specInfo = this.state.specInfo
    if (!specInfo[index]) {
      specInfo[index] = {}
    }
    var key = e.target.name
    var value = e.target.value
    if (key === 'amount') {
      if (value !== '' && !/^[1-9]\d*$/.test(value)) return;
    }else if (key === 'price') {
      if (value !== '' && !/^[1-9]\d*(\.\d{1,2}|)$/.test(value)) return;
    }
    specInfo[index][e.target.name] = e.target.value
    this.setState({
      specInfo
    })
  }

  /**
   *  img  src
   * @param {*} file
   */
  createPictureURL(file) {
    var windowURL = window.URL || window.webkitURL
    return windowURL.createObjectURL(file)
  }

  /**
   * 
   */
  smaillPictureChangeHandler(index, e) {
    var smaill = this.state.smaillPicture
    var files = e.target.files
    if (files.length === 0) {
      return;
    }else if (smaill[index] || files.length === 1) {
      smaill[index] = files[0]
    } else {
      for (var i = index, j = 0, len = files.length > 5 ? 5 : files.length; i < len && j < files.length; ++i && ++j) {
        if (!files[j]) {
          // 
          i--
          continue;
        }
        smaill[i] = files[j]
      }
    }
    // 
    if (smaill.length < 5 && smaill[smaill.length -1]) {
      smaill.push('')
    }
    this.setState({
      smaillPicture: smaill
    })
  }

  /**
   * 
   */
  infoPictureChangeHandler(index, e) {
    var info = this.state.infoPicture
    var files = e.target.files
    if (files.length === 0) {
      return;
    } else if (info[index] || files.length === 1) {
      info[index] = files[0]
    } else {
      for (var i = index, j = 0, len = files.length; j < len; ++i && ++j) {
        if (!files[j]) {
          // 
          i--
          continue;
        }
        info[i] = files[j]
      }
    }
    // 
    if (info[info.length - 1]) {
      info.push('')
    }
    this.setState({
      infoPicture: info
    })
  }

  /**
   *  Logo
   */
  logoPictureChangeHandler(e) {
    var files = e.target.files
    if (files.length === 0) return;
    this.setState({
      logo: files[0]
    })
  }

  /**
   * 
   * @param {*} index
   */
  smaillPictureDelete(index) {
    if ((index === 0 && this.state.smaillPicture.length === 1) || index >= this.state.smaillPicture.length) return;
    var smaillPicture = this.state.smaillPicture
    if (!smaillPicture[index]) {
      return;
    }
    smaillPicture.splice(index, 1)
    this.setState({
      smaillPicture
    })
    // console.log(index)
  }

  infoPictureDelete(index) {
    if ((index === 0 && this.state.infoPicture.length === 1) || index >= this.state.infoPicture.length) return;
    var infoPicture = this.state.infoPicture
    if (!infoPicture[index]) {
      return;
    }
    infoPicture.splice(index, 1)
    this.setState({
      infoPicture
    })

  }

  /**
   *  
   */
  filterProperties() {
    const props = this.state.properties
    let filtered = {
      name: [],
      value: []
    }
    // 
    for (var i = 0; i < props.length; i++) {
      var item = {name: '', value: []}
      if (props[i].name && filtered.name.findIndex(fname => fname === props[i].name) === -1) {
        item.name = props[i].name
        for (var j = 0; j < props[i].value.length; j++) {
          if (props[i].value[j] && props[i].value.indexOf(props[i].value[j], j + 1) === -1) {
            item.value.push(props[i].value[j])
          }
        }
        if (item.value.length > 0) {
          filtered.name.push(item.name)
          filtered.value.push(item.value)
        }
      }
    }
    return filtered
  }

  /**
   * 
   */
  submitProductIssue() {
    if (!this.props.categoryChoose[2] ||
        !/^[1-9]\d*$/.test(this.props.storeId) ||
        !this.state.goodName ||
        !this.state.goodFrom
      ) {
        return this.props.alertMessageAction('')
    }

    var specProp = this.filterProperties()
    // var len1 = specProp.value.reduce((prev,now) => prev * now.length,1)

    var form = new FormData()
    form.append('detailId', this.props.categoryChoose[2])
    form.append('storeId', this.props.storeId)
    form.append('goodFrom', this.state.goodFrom)
    form.append('nowPrice', this.state.specInfo[0].price)
    form.append('goodName', this.state.goodName)
    form.append('specName', JSON.stringify(specProp.name))
    form.append('specValue', JSON.stringify(specProp.value))
    form.append('goodDetailInfo', JSON.stringify(this.state.specInfo))
    form.append('logo', this.state.logo)
    this.state.smaillPicture.forEach((item) => {
      form.append('goodSmaillPicture', item)
    })
    this.state.infoPicture.forEach((item) => {
      form.append('goodInfoPicture', item)
    })
    axios.post(`${HOST}/api/goods/add_product`, form)
      .then(({ data }) => {
        console.log(data)
        if (!data.success) {
          return this.props.alertMessageAction(data.message)
        }
        var sure = () => this.props.history.push('/seller/panel/chk_products')
        this.props.confirmMessageAction('1-3', sure, sure)
      })
      .catch(err => {
        this.props.alertMessageAction(err.message || '')
      })
  }

  render() {
    const table = specValueArrToTableArr(this.filterProperties().value)

    return <div className="issue">
      <AppHeader />
      <div className="issue-head">
        <div className="ih-box">
          <span className="f1"></span>
          <span className="f2"></span>
        </div>
      </div>

      <div className="issue-body">
        <div className="issue-item f_wrap">
          <div className="iss-tit f_L" style={{border: 0}}></div>
          <div className="current-cate-wrap f_L">
            <div className="curr-cate-box f_wrap">
            {
              this.state.choose.map((category, index) => {
                return index === 0 ?
                        <div className="curr-item f_L" key={index}>{category.bigName}</div> :
                        index === 1 ?
                        <div className="curr-item f_L" key={index}><span className="curr-gt">&gt;</span><span>{category.smaillName}</span></div> :
                        <div className="curr-item f_L" key={index}><span className="curr-gt">&gt;</span><span>{category.detailName}</span></div>
              })
            }
            </div>
          </div>
        </div>
        <div className="issue-item">
          <div className="iss-tit"></div>
          <div className="iss-mini-wrap">
            <div className="iss-detail f_wrap">
              <div className="require f_L"></div>
              <div className="good-tit f_L"><input onChange={this.fieldChangeHandler} value={this.state.goodName} name="goodName" placeholder="" type="text"/></div>
            </div>
            <div className="iss-detail f_wrap">
              <div className="require f_L"></div>
              <div className="good-tit f_L"><input style={{width: '400px'}} onChange={this.fieldChangeHandler} value={this.state.goodFrom} name="goodFrom" placeholder="" type="text"/></div>
            </div>
            <div className="iss-detail">
              <div className="iss-spec f_wrap">
                <div className="require f_L"></div>
                <div>
                  <span className="f-important f_L">/</span>
                  <span style={{paddingLeft: '10px'}}>( - )</span>
                </div>
              </div>
              {/* start - */}
              <div className="prop-wrap">
                {
                  this.state.properties.map((prop, i) => (
                    <div className="iss-onece f_wrap" key={i}>
                      {/*  */}
                      <div className="prop-name-wrap f_L">
                        <div className="prop-name">
                          <label htmlFor={`prop${i}`}></label>
                          <input onBlur={this.propNameBlurHandler.bind(this,i)} onChange={this.propNameChangeHandler.bind(this,i)} id={`prop${i}`} value={prop.name} type="text"/>
                        </div>
                        {/*  */}
                        { i > 0 && <p onClick={this.propNameCancelHandler.bind(this,i)} title="" style={{background: `transparent url(${FILE_HOST}/image/ui/minus-circle.png) 0px 0px no-repeat`, backgroundSize: '16px 16px'}} className="prop-cancel"></p> }
                      </div>
                      {/*  */}
                      <div className="prop-value-wrap f_r f_wrap">
                        <div className="f_L"></div>
                        <div className="f_r">
                        {
                          prop.value.map((value, j) => (
                            <div className="prop-value" key={j}>
                              <input onBlur={this.propValueBlurHandler.bind(this,i,j)} onChange={this.propValueChangeHandler.bind(this,i,j)} value={value} type="text"/>
                              {/*  */}
                              { j > 0 && <p onClick={this.propValueCancelHandler.bind(this,i,j)} title="" style={{background: `transparent url(${FILE_HOST}/image/ui/minus-circle.png) 0px 0px no-repeat`, backgroundSize: '16px 16px'}} className="prop-cancel"></p> }
                            </div>
                          ))
                        }
                        </div>
                      </div>
                    </div>
                  ))
                }
              </div>
              {/* end - */}
            </div>
          </div>
        </div>

        <div className="issue-item">
          <div className="iss-tit"></div>
          <div className="iss-table-wrap">
            {
              table.length > 0 && <table className="issue-table" style={{margin: '0 auto'}} border="1" cellSpacing="0" cellPadding="0">
                <thead className="issue-thead">
                  <tr>
                    {
                      new Array(table[0].length).fill(0).map((_, index) => (
                        <td key={index}>{this.state.properties[index].name}</td>
                      ))
                    }
                    <td></td>
                    <td></td>
                  </tr>
                </thead>
                <tbody className="issue-tbody">
                  {
                    table.map((column, index) => (
                      <tr key={index}>
                        {
                          column.map((row, index) => (
                            <td rowSpan={row.rowspan} key={index}>{row.value}</td>
                          ))
                        }
                        <td><input onChange={this.specInfoChangeHandler.bind(this, index)} autoComplete="off" name="amount" placeholder="" type="text"/></td>
                        <td><input onChange={this.specInfoChangeHandler.bind(this, index)} autoComplete="off" name="price" placeholder="" type="text"/></td>
                      </tr>
                    ))
                  }
                </tbody>
              </table>
            }
          </div>
        </div>

        <div className="iss-pics-wrap issue-item">
          <div className="iss-tit"></div>
          <div className="iss-picture-wrap">
            <div className="iss-pic-item-wrap pic-smaill-wrap">
              <div className="iss-pic-tit"> 5</div>
              <div className="pic-item-wrap f_wrap">
                {
                  this.state.smaillPicture.map((p, index) => (
                    <div className="pic-item f_L" key={index}>
                      <img src={p ? this.createPictureURL(p) : ''}/>
                      <input key={index} onChange={this.smaillPictureChangeHandler.bind(this, index)} type="file" multiple={p === ''}/>
                      <div onClick={this.smaillPictureDelete.bind(this, index)} className="pic-cancel"></div>
                    </div>
                  ))
                }
              </div>
            </div>
            <div className="iss-pic-item-wrap">
              <div className="iss-pic-tit"></div>
              <div className="pic-item-wrap f_wrap">
                {
                  this.state.infoPicture.map((p, index) => (
                    <div className="pic-item f_L" key={index}>
                      <img src={p ? this.createPictureURL(p) : ''}/>
                      <input key={index} onChange={this.infoPictureChangeHandler.bind(this, index)} type="file" multiple={p === ''}/>
                      <div onClick={this.infoPictureDelete.bind(this, index)} className="pic-cancel"></div>
                    </div>
                  ))
                }
              </div>
            </div>
            <div className="iss-pic-item-wrap">
              <div className="iss-pic-tit"> LOGO</div>
              <div className="pic-item-wrap">
                <div className="pic-item">
                  <img src={this.state.logo ? this.createPictureURL(this.state.logo) : ''}/>
                  <input onChange={this.logoPictureChangeHandler.bind(this)} type="file"/>
                  <div className="pic-cancel"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="iss-submit"><button onClick={this.submitProductIssue}></button></div>
      </div>
      <Footer />
    </div>
  }
}

ProductIssue.propTypes = {
  categoryChoose: PropTypes.array.isRequired
}

function mapStateToProps(state) {
  return {
    type: state.goods.type,
    categoryChoose: state.goods.categoryChoose,
    storeId: state.auth.user.storeId
  }
}

export default connect(mapStateToProps, { alertMessageAction, confirmMessageAction })(ProductIssue)
