import React, { Component } from 'react'

function BacklogData(data) {
  return [
    {title: '', logs: [
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
    ]},
    {title: '', logs: [
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
    ]},
    {title: '', logs: [
      {itemName: '', amount: 0},
    ]},
    {title: '', logs: [
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
      {itemName: '', amount: 0},
    ]},
  ]
}

class Backlog extends Component {
  constructor(props) {
    super(props)

    this.state = {

    }
  }

  render() {
    const backlogData = new BacklogData()

    return (
      <div className="">
        <div className="da-head border-bottom"></div>
        <div className="log-body">
        {
          backlogData.map((data, index) => (
            <div className="backlog-item" key={index}>
              <div className="log-tit">{data.title}</div>
              <div className="f_wrap">
                {
                  data.logs.map((log, index) => (
                    <div className="log-item f_L f_wrap" key={index}>
                      <div className="f_L">{log.itemName}</div>
                      <div className="log-amount f_r">{log.amount}</div>
                    </div>
                  ))
                }
              </div>
            </div>
          ))
        }
        <div className="backlog-item">
          <div className="log-tit"></div>
          <div className="log-self">
            <span></span>
            <span style={{padding: '0 5px'}} className="log-amount">80</span>
            <span></span>
            <span>, </span>
          </div>
        </div>
        </div>
      </div>
    )
  }
}

export default Backlog

