import React, { Component } from 'react'
import classnames from 'classnames'
import { connect } from 'react-redux'

import './index.scss'
import { FILE_HOST } from '../../config/config'
import Footer from '../../components/Footer/index.jsx'
import AppHeader from '../../components/Header/index'
import Step1 from './subpage/Step1'
import Step2 from './subpage/Step2'
import Step3 from './subpage/Step3'

class Register extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
  }

  render() {
    return (
      <div className="regist">
        <AppHeader/>
        <div className="regist-head">
          <div className="regi-head-box f_wrap">
            <div className="f-logo f_L">
              <img src={`${FILE_HOST}/image/seller/seller-regi-logo.png`} alt="seller-logo"/>
            </div>
            <div className="f-tit f_L"></div>
          </div>
        </div>
        <div className="regist-body">
          <div className="step-wrap f_wrap">
            <div className={classnames("step-one f_L", {
              'f-flush': this.props.step >= 1,
            })}><span className="f-icon">1</span><span className="f-step"></span></div>

            <div className={classnames("step-one f_L", {
              'f-flush': this.props.step >= 2,
            })}><span className="f-icon">2</span><span className="f-step"></span></div>

            <div className={classnames("step-one f_L", {
              'f-flush': this.props.step === 3,
            })}><span className="f-icon">3</span><span className="f-step"></span></div>
          </div>
          <div>
            {
              this.props.step === 3 ? <Step3 /> :
              this.props.step === 2 ? <Step2 /> : <Step1 />
            }
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    step: state.register.step
  }
}

export default connect(mapStateToProps)(Register)
