import React, { Component } from 'react'
import Axios from 'axios';
import classnames from 'classnames'
import { connect } from 'react-redux'

import { isPhone, isLength } from '../../../utils/validator'
import { HOST, SELLER_HOST } from '../../../config/config'
import { alertMessageAction } from '../../../store/actions/messageActions'
import { step1FinishAction } from '../../../store/actions/registerActions'

class Step1 extends Component {
  constructor(props) {
    super(props)
    this.state = {
      phone: '',
      smscode: '',
      errors: {} //{phone: '', smscode: ''}
    }
    this.myState = {
      smsSending: false
    }

    this.fieldChangeHandler = this.fieldChangeHandler.bind(this)
    this.smsCodeClickHandler = this.smsCodeClickHandler.bind(this)
    this.validator = this.validator.bind(this)
    this.nextStepSubmit = this.nextStepSubmit.bind(this)
  }

  /**
   * 
   */
  validator() {
    var ans = {}
    if (!isPhone(this.state.phone)) {
      ans.phone = ''
    }
    if (!/^[1-9]\d{5}$/.test(this.state.smscode)) {
      ans.smscode = ''
    }
    return ans
  }

  fieldChangeHandler(e) {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  /**
   * 
   */
  smsCodeClickHandler(e) {
    if (this.myState.smsSending) {
      return this.props.alertMessageAction('')
    }
    if (this.validator().phone) {
      return this.props.alertMessageAction('')
    }

    var target = e.currentTarget
    target.style.backgroundColor = 'rgb(226,225,225)'
    target.style.color = 'rgba(148,139,139)'
    var interval = 0
    var time = 60
    Axios.post(`${HOST}/api/operator/sms`, { phone: this.state.phone })
      .then(({ data }) => {
        console.log(data)
        if (!data.success) {
          return this.setState({
            smscode: data.message
          })
        }

        // 
        interval = window.setInterval(() => {
          time--
          if (time <= 0) {
            target.innerText = ''
            target.style.backgroundColor = ''
            target.style.color = ''
            return window.clearInterval(interval)
          }
          target.innerText = '' + time
        }, 1000)
        this.props.alertMessageAction('')

      }).catch(err => {
        console.error(err)
      })

  }

  /**
   * 
   */
  nextStepSubmit() {
    if (Object.keys(this.validator()).length !== 0) {
      return this.props.alertMessageAction('')
    }

    Axios.post(`${SELLER_HOST}/api/seller/store/code`, {
      phone: this.state.phone,
      code: this.state.smscode
    }).then(({ data }) => {
      if (!data.success) {
        return this.setState({
          errors: { ...this.state.errors, smscode: data.message }
        })
      }
      // 
      this.props.step1FinishAction(this.state.phone, data.payload)

      }).catch(err => {
        this.setState({
          errors: { ...this.state.errors, smscode: err.message || '' }
        })
      })

  }

  render() {
    return <div className="step1-wrap">
      <div className="step1-item phone-item">
        <label htmlFor="phone"></label>
        <input className={classnames({
          letter: this.state.phone !== ''
        })} onChange={this.fieldChangeHandler} value={this.state.account} placeholder="" autoComplete="off" name="phone" type="text" id="phone"/>
      </div>
      {
        this.state.errors.phone && <div className="form-err">{this.state.errors.phone}</div>
      }
      <div className="step1-item sms-item">
        <label htmlFor="smscode"></label>
        <input className={classnames("sms-input", {
          letter: this.state.smscode !== ''
        })} onChange={this.fieldChangeHandler} value={this.state.smscode} placeholder="" autoComplete="off" name="smscode" type="text" id="smscode"/>
        <button className="sms-btn" onClick={this.smsCodeClickHandler}></button>
      </div>
      {
        this.state.errors.smscode && <div className="form-err">{this.state.errors.smscode}</div>
      }
      <button onClick={this.nextStepSubmit} className={classnames('next-btn', {
        'next-wait': Object.keys(this.validator()).length !== 0
      })}></button>
    </div>
  }
}

export default connect(null, { alertMessageAction, step1FinishAction })(Step1)
